import React from 'react';
import Head from 'next/head';

import styled from 'styled-components';

import '../css/artist.css';
import '../css/normalize.css';
import '../css/skeleton.css';

class Auditory extends React.Component{

	
	constructor(props){

	    super(props)
	    if(this.key === 1){;
			let initial = this.props.peter;
			this.state = {
				checked: initial.checked,
				adults1: initial.adults1,
				children1: initial.children1,
				adults2: initial.adults2,
				children2: initial.children2,
				adults3: initial.adults3,
				children3: initial.children3,
				adults4: initial.adults4,
				children4: initial.children4
			}
		}
		else{
			this.state = {
				checked: '',
				adults1: 1,
				children1: 0,
				adults2: 1,
				children2: 0,
				adults3: 1,
				children3: 0,
				adults4: 1,
				children4: 0
			}
		}	   
	}

	render(){

		return(
			<div className='artistArea'>
				<div className='artistBackground'>
					<img src={this.props.artist.display_src} className='artistMainPhoto' />	    
					<iframe className='artistVideo' src={this.props.artist.mv} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
					<div className='bio'>
					{this.props.artist.bio}
					</div>
				</div>
			</div>
		)
	}
}

export default Auditory;